@import url(https://fonts.googleapis.com/css?family=Open+Sans:600|Work+Sans:400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {

  text-align: center;
}

html {
  background-color: black;
}

.backg {
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  /* color: white; */
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
html {
    font-size: 100%;
}

#loginform {
    max-width: 500px;
    min-width: 300px;
    max-height: 700px;
    width: 30%;
    height: 60%;
    margin: 100px auto;
    background-color: white;
    border-radius: 25px;
}

#headerTitle {
    text-align: center;
    font-family: 'open sans', sans-serif;
    padding: 2rem 0;
    margin: 0;
    font-size: 2rem;
}

.row {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
    max-width: 100%;

}

.row input {
    width: 80%;
    box-sizing: border-box;
    border: none;
    font-size: 1rem;
    padding-left: 1.5rem;
    padding-bottom: 1rem;
    box-shadow: inset 0px -3px 0px 0px rgba(187, 187, 187, 0.2);
    transition: box-shadow 0.2s ease-in;
}

.row input:focus {
    box-shadow: inset 0px -3px 0px 0px rgba(34, 193, 195, 0.7);
    outline: none;
}

.row input::-webkit-input-placeholder {
    opacity: 1;
    -webkit-transition: opacity 0.25s ease-out;
    transition: opacity 0.25s ease-out;
}

.row input:hover::-webkit-input-placeholder,
.row input:focus::-webkit-input-placeholder {
    opacity: 0;
}

.row label {
    align-self: start;
    padding-left: 4rem;
    padding-bottom: 0.5rem;
    color: rgba(187, 187, 187, 0.9);
}

.row button {
    border-radius: 25px;
    width: 80%;
    height: 40px;
    font-size: 1.3rem;
    color: white;
    font-weight: 700;
    background: rgb(34, 193, 195);
    background: linear-gradient(90deg, rgba(34, 193, 195, 1) 0%, rgba(253, 187, 45, 1) 100%);
    border: 0px;
    cursor: pointer;
    transition: opacity 0.25s ease-out;
}

.row button:hover {
    opacity: 0.8;
}

#button {
    padding-bottom: 1.5rem;
}

#facebookIcon {
    background-image: url("https://image.flaticon.com/icons/svg/145/145802.svg");
    background-repeat: no-repeat;
    width: 3rem;
    height: 3rem;
}

#twitterIcon {
    background-image: url("https://image.flaticon.com/icons/svg/145/145812.svg");
    background-repeat: no-repeat;
    width: 3rem;
    height: 3rem;
}

#googleIcon {
    background-image: url("https://image.flaticon.com/icons/svg/145/145804.svg");
    background-repeat: no-repeat;
    width: 3rem;
    height: 3rem;
}

#iconGroup {
    display: flex;
    justify-content: center;
    padding: 2rem 0;
}

#iconGroup a {
    margin: 0 0.6rem;
}

#alternativeLogin {
    text-align: center;
    padding-top: 2rem;
    margin-top: 1.5rem;
}
.uwu {
    background-color: rgb(27, 104, 177);
}

.imagelogo {
    text-align: start;
    width: 70%;
    padding: auto;
    margin-left: 3rem
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.body {
    align-items: center;
    background: #D8AA96;
    color: rgba(0, 0, 0, 0.8);
    display: grid;
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    font-weight: 400;
    height: 100vh;
    justify-items: center;

}

.circle {
    margin: auto;
    background: black;
    border: white 5px;
    height: 150px;
    width: 150px;
    border-radius: 75px;
    /* margin: 20px; */
    /* box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 1); */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.signup-container {
    display: grid;
    grid-template-areas: "left right";
    max-width: 900px;
    margin: auto;
    margin-top: 2em;
}

.left-container {
    background: white;
    overflow: hidden;
    padding: 40px 0 0 0;
    position: relative;
    text-align: center;
    width: 300px;
    margin: 1em;
}

.left-container h1 {
    color: rgba(0, 0, 0, 0.8);
    display: inline-block;
    font-size: 24px;
}

.left-container h1 i {
    background: #F7B1AB;
    border-radius: 50%;
    color: #807182;
    font-size: 24px;
    margin-right: 5px;
    padding: 10px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.left-container .puppy {
    bottom: -5px;
    position: absolute;
    text-align: center;
}

.left-container .puppy:before {
    background: #807182;
    content: "";
    height: 100%;
    left: 0;
    opacity: 0.4;
    position: absolute;
    width: 100%;
    z-index: 1;
}

.left-container img {
    -webkit-filter: sepia(100%);
            filter: sepia(100%);
    width: 70%;
}

.right-container {
    background: #f9c7c2;
    /* display: flex; */
    grid-template-areas: "."".";
    width: 1800px;
    margin: 1em;
}

.right-container h1:nth-of-type(1) {
    color: rgba(0, 0, 0, 0.8);
    font-size: 28px;
    font-weight: 600;
}

.right-container .set {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
}

.right-container input {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    height: 38px;
    line-height: 38px;
    padding-left: 5px;
}

.right-container input,
.right-container label {
    color: rgba(0, 0, 0, 0.8);
}

.right-container header {
    padding: 40px;
}

.right-container label,
.right-container input,
.right-container .pets-photo {
    width: 176px;
}

.right-container .pets-photo {
    align-items: center;
    display: grid;
    grid-template-areas: ". .";
}

.right-container .pets-photo button {
    border: none;
    border-radius: 50%;
    height: 38px;
    margin-right: 10px;
    outline: none;
    width: 38px;
}

.right-container .pets-photo button i {
    color: rgba(0, 0, 0, 0.8);
    font-size: 16px;
}

.right-container .pets-weight .radio-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.right-container footer {
    align-items: center;
    background: #fff;
    display: grid;
    padding: 5px 40px;
}

.right-container footer button {
    border: 1px solid rgba(0, 0, 0, 0.2);
    height: 38px;
    line-height: 38px;
    width: 100px;
    border-radius: 19px;
    font-family: "Montserrat", sans-serif;
}

.right-container footer #back {
    background: #fff;
    transition: 0.2s all ease-in-out;
}

.right-container footer #back:hover {
    background: #171A2B;
    color: white;
}

.right-container footer #next {
    background: #807182;
    border: 1px solid transparent;
    color: #fff;
}

.right-container footer #next:hover {
    background: #171A2B;
}

.pets-name label,
.pets-breed label,
.pets-birthday label,
.pets-gender label,
.pets-spayed-neutered label,
.pets-weight label {
    display: block;
    margin-bottom: 5px;
}

.radio-container {
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    display: inline-block;
    padding: 5px;
}

.radio-container label {
    background: transparent;
    border: 1px solid transparent;
    border-radius: 2px;
    display: inline-block;
    height: 26px;
    line-height: 26px;
    margin: 0;
    padding: 0;
    text-align: center;
    transition: 0.2s all ease-in-out;
    width: 80px;
}

.radio-container input[type=radio] {
    display: none;
}

.radio-container input[type=radio]:checked+label {
    background: #F7B1AB;
    border: 1px solid rgba(0, 0, 0, 0.1);
}
